import { Component, OnInit } from '@angular/core';
import { FormGroup } from '@angular/forms';
import { OmniPullService } from 'src/app/commonBussinessSRC/psServices/omni-common/omni-pull.service';
import { PsKeyinDateComponent } from 'src/app/commonSRC/psComponents/ps-base/ps-base-field/ps-field-keyin/ps-keyin-date/ps-keyin-date.component';
import { LoggerService } from 'src/app/commonSRC/psServices/logger/logger.service';
import { PsCommonService } from 'src/app/commonSRC/psServices/ps-common/ps-common.service';
import { CommonBussinessConstant } from './../../commonBussinessSRC/psServices/models/ps-common-bussiness-constant';
import { IOptionsPsAgGrid } from './../../commonSRC/psComponents/ps-base/ps-ag-grid/ps-ag-grid.component.interfaces';
import { PsKeyinInputComponent } from './../../commonSRC/psComponents/ps-base/ps-base-field/ps-field-keyin/ps-keyin-input/ps-keyin-input.component';
import { ConstantCommon } from './../../commonSRC/psServices/models/common-constant';
import { IOptionsPsHyperlinkAnchor, IOptionsPsLabel, IOptionsPsTemplateView } from './../../commonSRC/psServices/models/ps-common-interface';
import { OmniBasePage } from './../omni-base/omni-base.page';
@Component({
    selector: 'loyalty-points-history',
    templateUrl: './loyalty-points-history.page.html'
})
export class LoyaltyPointsHistoryPage extends OmniBasePage implements OnInit {
    formGroup = new FormGroup({});
    loyaltyPointsHistoryVO = {};
    showLoyaltyPoints = false;
    templateViewOptions: IOptionsPsTemplateView = {
        group: this.formGroup,
        disableLoading: true
    };
    loyaltyPointsLabelOptions: IOptionsPsLabel = {
        labelKey: 'my_loyalty_points_miles_key'
    };
    loyaltyPointsOptions: IOptionsPsLabel = {};
    loyaltyBalanceOptions: IOptionsPsHyperlinkAnchor = {
        titleOptions: {
            translate: false
        }
    };
    redeemOptions: IOptionsPsHyperlinkAnchor = {
        pageOptions: {
            operId: CommonBussinessConstant.REWARDS_CATALOGUE_OPER,
            title: 'rewards_catalogue_key',
            iconName: 'rewards-catalogue',
        },
        titleOptions: {
            labelKey: 'redeem_now_key'
        }
    };
    loyaltyPointsHistoryOptions: IOptionsPsAgGrid = {
        fcName: 'loyaltyPointsHistory',
        group: this.formGroup,
        gridOptions: {
            url: CommonBussinessConstant.RETURN_POINTS_HISTORY_URL,
            defaultColDef: {
                flex: CommonBussinessConstant.GRID_FLEX,
                minWidth: CommonBussinessConstant.GRID_MIN_WIDTH,
                sortable: true,
                resizable: true,
            },
            rowSelection: 'multiple',
            applyPagination: true,
            paginationPageSize: CommonBussinessConstant.GRID_PAGE_SIZE,
            cacheBlockSize: CommonBussinessConstant.GRID_CACHE_BLOCK_SIZE,
            cacheOverflowSize: CommonBussinessConstant.GRID_CACHE_OVERFLOW_SIZE,
            maxConcurrentDatasourceRequests: CommonBussinessConstant.GRID_MAX_CONC_DATA,
            infiniteInitialRowCount: CommonBussinessConstant.GRID_INFINITE_INITIAL_ROW_COUNT,
            maxBlocksInCache: CommonBussinessConstant.GRID_MAX_BLOCKS_IN_CACHE
        },
        enableEdit: false,
        enableDelete: false,
        enableSort: true,
        enableFilter: true,
        headerCheckboxSelection: false,
        headerCheckboxSelectionFilteredOnly: false,
        checkboxSelection: false
    };
    constructor(public commonService: PsCommonService, public logger: LoggerService,
        public omniPull: OmniPullService) {
        super();
    }


    async ngOnInit() {
        super.init();
        this.getPointsBalance();
        this.loyaltyPointsHistoryOptions.columnComponents = [
            {
                columnNameKey: 'account_key',
                propertyName: 'pathName',
                component: PsKeyinInputComponent,
                componentOptions: {},
                componentType: 'input'
            },
            {
                columnNameKey: 'description_key',
                propertyName: 'pathDescription',
                component: PsKeyinInputComponent,
                componentOptions: {},
                componentType: 'input'
            },
            {
                columnNameKey: 'date_key',
                propertyName: 'pathTransactionDate',
                component: PsKeyinDateComponent,
                componentOptions: {},
                componentType: 'date',
            },
            {
                columnNameKey: 'earned_points_key',
                propertyName: 'pathNumberOfPoints',
                component: PsKeyinInputComponent,
                componentOptions: {},
                componentType: 'input'
            }
        ];
        const res = await this.omniPull.getParamValOf('DefaultDateFormat').catch(err => this.logger.log(err));
        if (res) {
            this.loyaltyPointsHistoryOptions.dateFormat = res.DefaultDateFormat ? res.DefaultDateFormat : ConstantCommon.DEFAULT_DATE_FORMAT;
        }
        this.baseFormGroup = this.formGroup;
        this.commonProv.setFormData(this.formGroup, this.loyaltyPointsHistoryVO);
    }

    getPointsBalance() {
        this.loyaltyPointsOptions.labelKey = CommonBussinessConstant.TOTAL_LOYALTY_POINTS;
        this.showLoyaltyPoints = true;
    }
}